import { Component, OnInit } from '@angular/core';
import { FormGroup,  FormBuilder,  Validators } from '@angular/forms';
import { AdduserService } from '../../adduser.service';

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.css']
})
export class CreateComponent implements OnInit {
  
  angForm: FormGroup;

  constructor(private adduserservice:AdduserService,private fb:FormBuilder) { 
    this.createForm();
  }

  createForm(){
    this.angForm = this.fb.group({
      employee_id: ['', Validators.required ],
      employee_name: ['', Validators.required ],
      email:['', Validators.required],
      company_name:['', Validators.required],
   });

  }

  addAddUser(employee_id,employee_name, email,company_name) {
    this.adduserservice.addAddUser(employee_id,employee_name, email,company_name);
}
  ngOnInit() {
  }

}
