import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup,  FormBuilder,  Validators } from '@angular/forms';
import { AddUser } from '../index/AddUser';
import { AdduserService } from '../../adduser.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {

  adduser: any = {};
  angForm: FormGroup;

  constructor(private route: ActivatedRoute,
    private router: Router,
    private adduserservice: AdduserService,
    private fb: FormBuilder) {
      this.createForm();
    }
    createForm() {
      this.angForm = this.fb.group({
            employee_id: ['', Validators.required ],
            employee_name: ['', Validators.required ],
            email: ['', Validators.required ],
            company_name: ['', Validators.required ]
         });
      }

    updateAddUser(employee_id, employee_name, email, company_name) {
      this.route.params.subscribe(params => {
          this.adduserservice.updateAddUser(employee_id, employee_name, email, company_name, params['id']);
          this.router.navigate(['index']);
      });
    }

    ngOnInit() {
      this.route.params.subscribe(params => {
        this.adduserservice.editAddUser(params['id']).subscribe(res => {
          this.adduser = res;
      });
    });
  }
}
