import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AddUser } from './components/index/AddUser';

@Injectable({
  providedIn: 'root'
})
export class AdduserService {

  uri = 'http://localhost:4000/addusers';

  constructor(private http: HttpClient) { }

  addAddUser(employee_id, employee_name, email, company_name) {
    const obj = {
      employee_id: employee_id,
      employee_name: employee_name,
      email: email,
      company_name: company_name
    };
    this.http.post(`${this.uri}/add`, obj)
        .subscribe(res => console.log('Done'));
  }

  getAddUsers() {
    return this
           .http
           .get(`${this.uri}`);
}

editAddUser(id) {
  return this
            .http
            .get(`${this.uri}/edit/${id}`);
}

updateAddUser(employee_id, employee_name, email, company_name, id) {

  const obj = {
    employee_id: employee_id,
    employee_name: employee_name,
    email: email,
    company_name: company_name
  };
  this
    .http
    .post(`${this.uri}/update/${id}`, obj)
    .subscribe(res => console.log('Done'));
}

  deleteAddUser(id) {
    return this
              .http
              .get(`${this.uri}/delete/${id}`);
  }
}
